.app__about{
    flex:1;
    width: 100%;
    flex-direction:column;
}
.app__profiles{
    display: flex;
    justify-content: center;
    align-items:flex-start;
    flex-wrap: wrap;
    margin-top:2rem;
}
.app__profiles-item{
    width:190px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin:2rem;

    img{
        width:100%;
        height:170px;
        object-fit: cover;
        border-radius: 20px;
    }

    @media screen and (min-width: 2000px){
        width:370px;
        margin: 2rem 4rem;

        img{
            height:320px;
        }
    }
}